import { useState, useEffect } from "react";

// import FormMini from "../FormMini/FormMini";

import { FaUserCircle } from "react-icons/fa";
import { HiMail } from "react-icons/hi";
import { VscChromeClose } from "react-icons/vsc";
import { Link } from "react-router-dom";
import ModalContainer from "../ModalContainer/ModalContainer";

export default function ModalHome() {
  const [classLeave, setClassLeave] = useState(false);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");

  const bodyEl = document.querySelector("body");

  useEffect(() => {
    add();
  }, []);

  const add = () => {
    bodyEl.addEventListener("mouseleave", onMouseLeave);
    window.addEventListener("keydown", escClose);
  };

  const escClose = (e) => {
    if (e.code === "Escape") {
      onClose();
    }
    window.removeEventListener("keydown", escClose);
  };

  function onMouseLeave(e) {
    if (e.toElement === null) {
      bodyEl.classList.add("overHiden");
      setClassLeave(true);
    }
  }

  const onClose = () => {
    window.removeEventListener("mouseleave", onMouseLeave);
    bodyEl.classList.remove("overHiden");
    setClassLeave(false);
  };

  const hendleClickBackdrop = (e) => {
    if (e.currentTarget === e.target) {
      onClose();
    }
  };

  const onChange = (e) => {
    const { name, value } = e.currentTarget;
    switch (name) {
      case "name":
        setName(value);

        break;
      case "email":
        setEmail(value);

        break;

      default:
        break;
    }
  };

  const dataSend = (e) => {
    e.preventDefault();
    // emailSend(name, email);
  };

  return (
    classLeave && (
      <ModalContainer onClose={hendleClickBackdrop}>
        <div className="box-icon-modal">
          <button type="button" className="btn-close-modal" onClick={onClose}>
            <VscChromeClose className="icon-modal" />
          </button>
        </div>
        <h1 className="heading-modal-home">
          –ù–∞—á–Ω–∏—Ç–µ –ø–æ–ª—É—á–∞—Ç—å –æ—Ç 2500 —É.–µ. –≤ –º–µ—Å—è—Ü!
        </h1>
        <div className=" modal-home-box ">
          <p className="p-modal-home">
            –ü—Ä–æ–π–¥–∏—Ç–µ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é –∏ –ø–æ–ª—É—á–∏—Ç–µ –±–µ—Å–ø–ª–∞—Ç–Ω—ã–π –¥–æ—Å—Ç—É–ø –∫ –ø—Ä–æ–≥—Ä–∞–º–º–µ!
          </p>

          <form className="form-modal" onSubmit={dataSend}>
            <div className="box-input-flex">
              <div className="container-input-modal ">
                <label className="form-lable" htmlFor="20"></label>
                <input
                  className="form-input-name"
                  type="text"
                  id="20"
                  name="name"
                  value={name}
                  onChange={onChange}
                  placeholder="–í–∞—à–µ –∏–º—è"
                />
                <FaUserCircle className="icon-form-user" />
              </div>
              <div className="container-input-modal ">
                <label htmlFor="email"></label>

                <input
                  className="form-input-email"
                  type="email"
                  id="email"
                  name="email"
                  value={email}
                  onChange={onChange}
                  placeholder="–í–∞—à –∞–∫—Ç—É–∞–ª—å–Ω—ã–π e-mail"
                />
                <HiMail className="icon-form-email" />
              </div>
            </div>

            <div className="box-modal-btn">
              <Link type="submit" className="btn-modal-home" to="/registration">
                <span className="text-btn-modal">–ü–æ–ª—É—á–∏—Ç—å –¥–æ—Å—Ç—É–ø</span>
              </Link>
              <div className="form-policy-container">
                <input
                  type="checkbox"
                  className="policy-checkbox"
                  defaultChecked="true"
                />
                <span className="policy-text">
                  –Ø —Å–æ–≥–ª–∞—Å–µ–Ω –Ω–∞ –æ–±—Ä–∞–±–æ—Ç–∫—É –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö –∏ –ø–æ–ª—É—á–µ–Ω–∏–µ
                  —Ä–µ–∫–ª–∞–º–Ω—ã—Ö –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤, –∏ —è —Å–æ–≥–ª–∞—Å–µ–Ω —Å
                  <Link to="/policy" target="_blank" className="policy-link">
                    –ø—É–±–ª–∏—á–Ω–æ–π –æ—Ñ–µ—Ä—Ç–æ–π
                  </Link>
                </span>
              </div>
            </div>
          </form>
        </div>
      </ModalContainer>
    )
  );
}
